@import "./variables";
.about-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  height: max-content;
  padding-top: 10rem;
  position: relative;
  .about-left {
    padding: 3rem;
    img {
      width: 18rem;
      height: auto;
      border-radius: 0.6rem;
      opacity: 0.55;
    }
  }
  .about-right {
    display: flex;
    flex-direction: column;
    h1.about-heading {
      font-size: 5rem;
      font-family: $monoFont;
      font-weight: 500;
      color: $white;
      padding: 2rem;
    }
    p.about-content {
      padding: 0 2rem 2rem 2rem;
      font-size: 2rem;
      font-family: $monoFont;
      font-weight: 400;
      color: transparentize($white, 0.12);
    }
  }
}

@media screen and (max-width: 945px) {
  .about-container {
    padding-top: 3rem;
    flex-direction: column;
    .about-left {
      padding: 2rem;
    }
    .about-right {
      p.about-content {
        font-size: 1.8rem;
      }
    }
  }
}

@media screen and (min-height: 900px) {
  #about {
  }
}
