@import "./variables";

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  background-color: $black;
  color: $white;
  width: 100%;
  min-height: 100vh;
}

.pages-container {
  background-color: $black;
  color: $white;
}

.page-section {
  width: 100%;
  min-height: 100vh;
}

#home {
  min-height: 0;
  height: calc(100vh - 6rem);
}

#about {
  min-height: 90vh;
  height: max-content;
}

@import "./nav";
@import "./hero";
@import "./projects";
@import "./about";
@import "./footer";
